@media (max-width:1200px) {

    .story {
        &__block {
            font-size: 20px;
        }
    }

    .admin {
        &__descr {
            font-size: 20px;
        }
    }

    .exponats {
        &__slide {
            img {
                max-height: 300px;
                min-height: 300px;
            }
        }

        &__slide-title {
            font-size: 25px;
        }

        &__descr {
            font-size: 18px;
            height: 105px;
        }
    }

    .map {
        &__block {
            iframe {
                height: 500px;
            }
        }
    }

    .form {
        &__wrapper {
            padding: 0 20px;
            gap: 10px;
        }

        img {
            display: none;
        }


        &__block {
            width: 100%;
        }
    }
}

@media (max-width:993px) {

    .title {
        font-size: 32px;
    }

    .story {
        margin-top: 50px;

        &__block {
            font-size: 18px;
            align-items: center;
        }
    }

    .admin {
        &__descr {
            font-size: 18px;
        }
    }

    .subscribe {
        &__wrapper {
            width: 100%;
        }
    }

}


@media (max-width:993px) {

    .title {
        font-size: 26px;
    }

    .story {
        margin-top: 50px;

        &__image {
            width: 75%;
        }

        &__block {
            font-size: 18px;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;

            p {
                width: 75%;
            }

        }
    }

    .question {
        &__block {
            flex-wrap: wrap;
            flex-direction: column !important;
            width: 100%;
        }


        &__image {
            width: 100%;

            img {
                margin: 0 auto;
            }
        }

        &__text {
            width: 100%;
        }
    }

    .admin {
        &__block {
            flex-wrap: wrap;

            div {
                width: 100%;
            }
        }

        &__descr {
            font-size: 18px;
        }
    }


}

@media (max-width:568px) {

    .story {

        &__block {
            font-size: 16px;

            p {
                width: 100%;
            }
        }
    }

    .exponats {
        &__descr {
            font-size: 16px;
            height: 100px;
        }

        &__slide-title {
            font-size: 22px;
        }
    }

    .title {
        text-align: center;
        font-size: 20px !important;
    }

    .form {
        &__block {
            input {
                width: 100%;
            }

            select {
                width: 100%;

                option {
                    font-size: 12px;
                }
            }
        }
    }


    .question {
        &__text {
            font-size: 14px;
        }

        &__buttons {
            flex-direction: column-reverse;

            button {
                width: 100%;
                height: 35px;
                margin-top: 15px;
            }
        }


        &__text-big {
            font-size: 30px;
        }

        &__image {
            width: 100%;
        }
    }

    .map {
        &__block {
            iframe {
                height: 300px;
            }
        }
    }

    .subscribe {
        &__block {

            input {
                width: 100%;
            }
        }
    }

}